<template>
    <!-- -->
    <div class="todo-card">
        <template v-if="cardType==1">
            <div class="todo-l">
                <p>{{cardData.time}}</p>
                <p>{{cardData.uname}}</p>
                <p>{{cardData.status==0?"":(cardData.status==1?"":(cardData.status==2?"":""))}}</p>
                <cube-button :inline="true" @click="wenzhen"></cube-button>
            </div>
            <div class="todo-r">
                <div class="todo-bg" :style="`background-image:url(${cardData.avatar})`"></div>
            </div>
            <div class="todo-status">{{cardData.method==1?"":""}}</div>
        </template>
        <template v-else-if="cardType==2">

        </template>
        <template v-else-if="cardType==3">
            <div class="todo-l">
                <p>{{cardData.time}}</p>
                <p>{{cardData.notice}}</p>
            </div>
            <div class="todo-r">
                <div class="todo-bg" :style="`background-image:url(${cardData.avatar})`"></div>
            </div>
        </template>
        <template v-else-if="cardType==4">
            <div class="todo-l">
                <p>{{cardData.time}}</p>
                <p>{{cardData.history}}</p>
                <p>{{cardData.hid}}</p>
            </div>
            <div class="todo-r">
                <div class="todo-bg" :style="`background-image:url(${cardData.avatar})`"></div>
            </div>
        </template>
        <template v-else-if="cardType==5">

        </template>
        <template v-else-if="cardType==6">
             <div class="todo-l">
                 <p>{{cardData.iname}}</p>
                <p>{{cardData.time}}</p>
            </div>
            <div class="todo-r">
                <div class="todo-bg" :style="`background-image:url(${cardData.avatar})`"></div>
            </div>
        </template>
        <template v-else-if="cardType==7">

        </template>
    </div>
</template>
<script>
// cardType 1  2  3  4  5  6  7 
export default {
    props:{
        cardType:{
            type:[String, Number],
            default:1
        },
        cardData:{
            type:Object,
            default:function(){
                return {}
            }
        }
    },
    methods:{
            wenzhen(){
                this.$router.push("/start")
            }
        }
}
</script>
<style lang="stylus" scoped>
.todo-card
    width calc(100% - 100px)
    box-sizing border-box
    border 1px solid #333333
    position relative
    display flex
    padding 10px 15px
    justify-content space-around
    .todo-l
        font-size 14px
        line-height 1.5
    .todo-r
        .todo-bg 
            width 90px
            height 90px
            background-position center
            background-size cover
            background-repeat no-repeat
    .todo-status
        width 30px
        height 30px
        font-size: 12px
        border-radius 50%
        line-height 30px
        position absolute
        top 45px
        left -15px
        text-align center
        background-color green 
        color #ffffff
</style>





