                    </cube-form-item>
                </cube-form-group>
                    <!-- <cube-form-item :field="fields[1]"></cube-form-item>
                    <cube-form-item :field="fields[2]"></cube-form-item>
                    <cube-form-item :field="fields[3]"></cube-form-item>
                    <cube-form-item :field="fields[4]"></cube-form-item>
                    <cube-form-item :field="fields[5]"></cube-form-item>
                    <cube-form-item :field="fields[6]"></cube-form-item>
                    <cube-form-item :field="fields[7]"></cube-form-item>
                    <cube-form-item :field="fields[8]"></cube-form-item>
                    <cube-form-item :field="fields[9]"></cube-form-item>
                    <cube-form-item :field="fields[10]">
                        <div class="pwd-div">
                            <cube-input v-model="model.pwd" placeholder=""></cube-input>
                            <cube-button :inline="true"></cube-button>
                        </div>
                    </cube-form-item>
                    <cube-form-item :field="fields[11]">
                        <cube-checkbox v-model="model.agree">
                            <p style="font-size:13px;"> <span style="color:red;">24</span></p>
                        </cube-checkbox>
                    </cube-form-item> -->
                <cube-form-group>
                    <cube-button class="fst-btn" @click="toNext">()</cube-button>
                </cube-form-group>
            </cube-form>
            <!-- <div class="align-group">
                <div class="a-l">
                    <label for="reserve"></label>
                </div>
                <div class="a-r">
                    <cube-input type="text" name="reserve" id="reserve" ></cube-input>
                </div>
            </div>
            <div class="align-group">
                <div class="a-l">
                    <label for="tohome"></label>
                </div>
                <div class="a-r">
                    <input type="text" name="tohome" id="tohome" />
                </div>
            </div>
            <div class="align-group">
                <div class="a-l">
                    <label for="consult"></label>
                </div>
                <div class="a-r">
                    <input type="text" name="consult" id="consult" />
                </div>
            </div>
            <div class="border"></div>
            <div class="align-group">
                <div class="a-l">
                    <label for="family"></label>
                </div>
                <div class="a-r">
                    <input type="text" name="family" id="family" />/
                </div>
            </div>
            <div class="align-group">
                <div class="a-l">
                    <label></label>            
                </div>
                <div class="a-r sel-times">
                    <input type="checkbox" name="mfgh" id="mfgh">
                    <label for="mfgh"></label>
                    <input type="text" name="ghtimes" id="ghtimes" value="30"/>
                    <br>
                    <input type="checkbox" name="smzl" id="smzl">
                    <label for="smzl"></label>
                    <input type="text" name="zltimes" id="zltimes" value="1"/>
                    <br>
                    <input type="checkbox" name="twzx" id="twzx">
                    <label for="twzx"></label>
                    <input type="text" name="zxtimes" id="zxtimes" value="30"/>
                </div>
                
            </div>
            <cube-button class="fst-btn" @click="toNext">()</cube-button> -->
            <!-- <button class="fst-btn u-btn" @click="toNext">()</button> -->
        </div>
        <!--  -->
        <div class="sec-cfg  main-box" v-show="issec">

            <div class="align-group">
                <div class="a-l">
                    <label>:</label>
                </div>
                <div class="a-r zl-times">
                    <cube-select
                    v-model="model2.morning.start"
                    :options="zltime.morning.start"
                    class="zl-items"
                    >
                    </cube-select>
                    -
                    <cube-select
                    v-model="model2.morning.end"
                    :options="zltime.morning.end"
                    class="zl-items"
                    >
                    </cube-select>
                    <cube-input v-model="model2.morning.times" type="number" class="zl-items">
                        <span slot="append" style="padding-left: 10px;"></span>
                    </cube-input>
                    <!-- <input type="text" v-model="model2.morning.times" class="am-person">  -->
                </div>
            </div>
             <div class="align-group">
                <div class="a-l">
                    <label>:</label>
