<template>
    <div class="register-div">
        <m-header title="" :show='ishow'></m-header>
        <div class="reg-form main-box">
            <cube-form
            :model="model">
                <cube-form-group>
                    <cube-form-item :field="fields[0]"></cube-form-item>
                    <cube-form-item :field="fields[1]"></cube-form-item>
                    <cube-form-item :field="fields[2]"></cube-form-item>
                    <cube-form-item :field="fields[3]"></cube-form-item>
                    <cube-form-item :field="fields[4]"></cube-form-item>
                    <cube-form-item :field="fields[5]"></cube-form-item>
                    <cube-form-item :field="fields[6]"></cube-form-item>
                    <cube-form-item :field="fields[7]"></cube-form-item>
                    <cube-form-item :field="fields[8]"></cube-form-item>
                    <cube-form-item :field="fields[9]"></cube-form-item>
                    <cube-form-item :field="fields[10]">
                        <div class="pwd-div">
                            <cube-input placeholder=""></cube-input>
                            <cube-button :inline="true"></cube-button>
                        </div>
                    </cube-form-item>
                    <cube-form-item :field="fields[11]">
                        <cube-checkbox v-model="model.agree">
                            <p style="font-size:13px;"> <span style="color:red;">24</span></p>
                        </cube-checkbox>
                    </cube-form-item>
            </cube-form-group>
            </cube-form>
        </div>
        <cube-button @click="toNext"></cube-button>
    </div>
</template>
<script>
import MHeader from 'components/m-header/m-header'
import{setItem,getItem} from 'common/js/utils'
export default {
    data(){
        return {
            identity:"0",
            ishow:false,
            model:{
                avatar:[],
                username:"",
                hospital:"",
                department:1,
                identity:1,
                office:1,
                idcard:[],
                zgcard:[],
                zccard:[],
                phone:"",
                pwd:"",
                agree:true  
            },
             fields:[
                    {
                        type:'upload',
                        modelKey:'avatar',
                        label:'',
                        events:{
                            'file-removed': (...args) => {
                                console.log('file removed', args)
                            }
                        },
                        rules: {
                            required: true,
                            uploaded: (val, config) => {
                                return Promise.all(val.map((file, i) => {
                                return new Promise((resolve, reject) => {
                                    if (file.uploadedUrl) {
                                    return resolve()
                                    }
                                    // fake request
                                    setTimeout(() => {
                                    if (i % 2) {
                                        reject(new Error())
                                    } else {
                                        file.uploadedUrl = 'uploaded/url'
                                        resolve()
                                    }
                                    }, 1000)
                                })
                                })).then(() => {
                                return true
                                })
                            }
                        },
                    },
                    {
                        type: 'input',
                        modelKey:'username',
                        label:'',
                        rules: {
                            required: true
                        },
                        trigger: 'blur'
                    },
                    {
                        type: 'input',
                        modelKey:'hospital',
                        label:'',
                        rules: {
                            required: true
                        },
                        trigger: 'blur'
                    },
                    {
                        type: 'select',
                        modelKey:'department',
                        label:'',
                         props: {
                            options: [{value:1,text:""},{value:2,text:""},{value:3,text:""}]
                        },
                        rules: {
                            required: true
                        }
                    },
                    {
                        type: 'select',
                        modelKey:'identity',
                        label:'',
                         props: {
                            options: [{value:1,text:""},{value:2,text:""}]
                        },
                        rules: {
                            required: true
                        }
                    },
                    {
                        type: 'select',
                        modelKey:'office',
                        label:'',
                         props: {
                            options: [{value:1,text:""},{value:2,text:""},{value:3,text:""},{value:3,text:""}]
                        },
                        rules: {
                            required: true
                        }
                    },
                    {
                        type:'upload',
                        modelKey:'idcard',
                        label:'',
                        events:{
                            'file-removed': (...args) => {
                                console.log('file removed', args)
                            }
                        },
                        rules: {
                            required: true,
                            uploaded: (val, config) => {
                                return Promise.all(val.map((file, i) => {
                                return new Promise((resolve, reject) => {
                                    if (file.uploadedUrl) {
                                    return resolve()
                                    }
                                    // fake request
                                    setTimeout(() => {
                                    if (i % 2) {
                                        reject(new Error())
                                    } else {
                                        file.uploadedUrl = 'uploaded/url'
                                        resolve()
                                    }
                                    }, 1000)
                                })
                                })).then(() => {
                                return true
                                })
                            }
                        },
                    },
                    {
                        type:'upload',
                        modelKey:'zgcard',
                        label:'',
                        events:{
                            'file-removed': (...args) => {
                                console.log('file removed', args)
                            }
                        },
                        rules: {
                            required: true,
                            uploaded: (val, config) => {
                                return Promise.all(val.map((file, i) => {
                                return new Promise((resolve, reject) => {
                                    if (file.uploadedUrl) {
                                    return resolve()
                                    }
                                    // fake request
                                    setTimeout(() => {
                                    if (i % 2) {
                                        reject(new Error())
                                    } else {
                                        file.uploadedUrl = 'uploaded/url'
                                        resolve()
                                    }
                                    }, 1000)
                                })
                                })).then(() => {
                                return true
                                })
                            }
                        },
                    },                            
                    {
                        type:'upload',
                        modelKey:'zccard',
                        label:'',
                        events:{
                            'file-removed': (...args) => {
                                console.log('file removed', args)
                            }
                        },
                        rules: {
                            required: true,
                            uploaded: (val, config) => {
                                return Promise.all(val.map((file, i) => {
                                return new Promise((resolve, reject) => {
                                    if (file.uploadedUrl) {
                                    return resolve()
                                    }
                                    // fake request
                                    setTimeout(() => {
                                    if (i % 2) {
                                        reject(new Error())
                                    } else {
                                        file.uploadedUrl = 'uploaded/url'
                                        resolve()
                                    }
                                    }, 1000)
                                })
                                })).then(() => {
                                return true
                                })
                            }
                        },
                    },
                    {
                        type: 'input',
                        modelKey:'phone',
                        props:{
                            placeholder:""
                        },
                        rules: {
                            required: true
                        },
                        trigger: 'blur'
                    },
                    {
                        type: 'input',
                        modelKey:'pwd',
                        props:{
                            placeholder:""
                        }
                    },
                    {
                        type: 'checkbox',
                        modelKey:'agree',
                        props:{
                            value: true
                        },
                        rules: {
                            required: true
                        }
                    }
                    
                ]

        }
    },
    create(){
        if(getItem("identity")){
            console.log("")
        }
    },
    components: {
        MHeader
    },
    methods:{
        toNext(){
            setItem("identity",this.model.identity)
            if(this.model.identity==1){
                this.$router.push("/doconfig")
            }else if(this.model.identity==2){
                this.$router.push('/nuconfig')
            }else{
                alert("error")
            }
            
        }
    }
}
</script>
<style lang="stylus" scoped>
@import "~common/stylus/variable"
.guize
    font-size $font-size-medium
.reg-btn
    display block
    margin 0 auto
.pwd-div
    position relative
    button
        position absolute
        right 0
        top 0
// .register-div
//     width 100%
//     position relative
//     height 100%
//     overflow-x hidden
//     .align-group
//         box-sizing border-box
//         width 100%
//         padding 10px
//         input:not([type="checkbox"])
//             height 25px 
//             width 60%
//             border 1px solid #333
//     .check-agree
//         font-size $font-size-small
//     .reg-btn
//         display block
//         margin 0 auto
</style>





