<template>
    <!--  -->
    <div class="p-detail">
        <m-header :title="mt"></m-header>
        <h-header :ishow="true"></h-header>
        <div class="his-box">
            <div class="flex-box his-desc-btn" v-show="iscf || isyz">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <cube-button @click="showHis">0001</cube-button>
                </div>
            </div>
            <div class="flex-box his-desc" v-show="!iscf && !isyz">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <p></p>
                </div>
            </div>
             <div class="flex-box his-desc" v-show="!iscf && !isyz">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <p></p>
                </div>
            </div>
            <div class="flex-box his-desc" v-show="!iscf && !isyz">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <p></p>
                </div>
            </div>
            <div class="flex-box recipe-btn" v-show="!iscf">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <cube-button @click="showCf">0001</cube-button>
                </div>
            </div>
            <div class="flex-box recipe" v-show="iscf">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <p></p> 
                </div>
            </div>
            <div class="flex-box advice-btn" v-show="!isyz">
                <div class="f-l">
                    <span></span>
                </div>
                <div class="f-r">
                    <cube-button @click="showYz">0001</cube-button>
                </div>
            </div>
            <div class="flex-box advice" v-show="isyz">
                <div class="f-l">
                    <p>2018-06-29</p>  
                    <p></p>
                    <p></p>
                    <p></p>
                    <p>zhanggk</p>
                </div>
                <div class="f-r">
                    <img src="http://www.gengkai.site/assets/images/avatar.jpg" alt="">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import MHeader from 'components/m-header/m-header'
import HHeader from 'components/m-header/h-header'
export default {
    data(){
        return {
            mt:"",
            isyz:false,
            iscf:false
        }
    },
    methods:{
        showYz(){
            this.mt=""
            this.isyz = true
            this.iscf = false
        },
        showHis(){
            this.mt=""
            this.isyz = false
            this.iscf = false
        },
        showCf(){
            this.mt=""
            this.iscf = true
            this.isyz = false
        }
    },
    components:{
        MHeader,
        HHeader
    }
}
</script>
<style lang="stylus" scoped>
.flex-box
    width 100%
    display flex
    justify-content space-around
    align-items center
    margin-top 10px
    .f-l
        width 30%
    .f-r
        width 60%
        text-align center 
.advice
    box-sizing border-box
    padding 10px
    border 1px solid #999999
    .f-l
        width 50%
        p
            line-height 2
    .f-r
        width 50%
        img 
            width 50%
    
   
</style>





